/* app.css */

html {
  scroll-behavior: smooth;
  scroll-padding-top: 100px; /* This accounts for the fixed navigation */
}

:root {
  --primary: #0a192f;
  --secondary: #64ffda;
  --text-primary: #ccd6f6;
  --text-secondary: #8892b0;
  --bg-light: #112240;
  --bg-lighter: #233554;
  --white: #e6f1ff;
  --transition: all 0.3s cubic-bezier(0.645, 0.045, 0.355, 1);
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, sans-serif;
  background: linear-gradient(135deg, #1a1a2e 0%, #16213e 100%);
  color: #e6e6e6;
  min-height: 100vh;
  line-height: 1.6;
}

section {
  padding: 100px 0;
  transition: opacity 0.3s ease-in-out;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 2rem;
}

/* Scrollbar styling */
::-webkit-scrollbar {
  width: 8px;
}

::-webkit-scrollbar-track {
  background: var(--primary);
}

::-webkit-scrollbar-thumb {
  background: var(--bg-lighter);
  border-radius: 4px;
}

::-webkit-scrollbar-thumb:hover {
  background: var(--secondary);
}

/* Add loading animation styles */
.loading-screen {
  height: 100vh;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background: var(--primary);
}

.loader {
  width: 50px;
  height: 50px;
  border: 3px solid var(--bg-lighter);
  border-radius: 50%;
  border-top-color: var(--secondary);
  animation: spin 1s ease-in-out infinite;
}

@keyframes spin {
  to { transform: rotate(360deg); }
}

/* Add Back to Top button styles */
.back-to-top {
  position: fixed;
  bottom: 2rem;
  right: 2rem;
  background: var(--bg-light);
  color: var(--secondary);
  width: 45px;
  height: 45px;
  border-radius: 50%;
  border: none;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.2rem;
  transition: var(--transition);
  z-index: 999;
}

.back-to-top:hover {
  background: var(--bg-lighter);
  transform: translateY(-5px);
}

